import { useState, useEffect, useCallback } from "react";
import axios from "axios";

const Gallery = () => {
  const [photos, setPhotos] = useState([]);
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(true);
  const [openEvents, setOpenEvents] = useState({});
  const [selectedEvent, setSelectedEvent] = useState(null);
  const [allEvents, setAllEvents] = useState(["General"]);
  const [refreshKey, setRefreshKey] = useState(0);

  // Fetch photos function
  const fetchPhotos = useCallback(async () => {
    try {
      setLoading(true);
      const response = await axios.get("http://localhost:5000/api/photos");
      
      const photoData = response.data.photos || response.data;
      console.log('Fetched photos:', photoData);

      // Process photos to ensure URLs are correct
      const processedPhotos = photoData.map(photo => ({
        ...photo,
        url: photo.url.startsWith('http') ? photo.url : `http://localhost:5000${photo.url}`
      }));

      setPhotos(processedPhotos);
      setError("");

      // Extract unique event names from photos
      const eventNames = [...new Set(processedPhotos.map(photo => photo.eventTitle || "General"))];
      const sortedEvents = ["General", ...eventNames.filter(name => name !== "General")];
      setAllEvents(sortedEvents);

      // If no event is selected, select the first one
      if (!selectedEvent && sortedEvents.length > 0) {
        setSelectedEvent(sortedEvents[0]);
      }

    } catch (err) {
      console.error("Error fetching photos:", err);
      setError("Failed to fetch gallery photos. Please try again later.");
    } finally {
      setLoading(false);
    }
  }, [selectedEvent]);

  // Initial data fetch
  useEffect(() => {
    fetchPhotos();
  }, [fetchPhotos, refreshKey]);

  // Refresh function
  const refreshGallery = useCallback(() => {
    setRefreshKey(prev => prev + 1);
  }, []);

  // Poll for updates every 30 seconds
  useEffect(() => {
    const intervalId = setInterval(refreshGallery, 30000);
    return () => clearInterval(intervalId);
  }, [refreshGallery]);

  // Group photos by events
  const groupedPhotos = allEvents.reduce((acc, evt) => {
    acc[evt] = photos.filter(photo => (photo.eventTitle || "General") === evt);
    return acc;
  }, {});

  // Handle image loading errors
  const handleImageError = (e, photo) => {
    console.error('Image load error:', photo);
    if (!e.target.dataset.retried) {
      e.target.dataset.retried = 'true';
      // Try to construct a corrected URL
      const correctedUrl = `http://localhost:5000/uploads/events/${photo.category}/${photo.eventId}/photos/${photo.filename}`;
      console.log('Attempting with corrected URL:', correctedUrl);
      e.target.src = correctedUrl;
    } else {
      // If corrected URL fails, use placeholder
      e.target.src = 'http://localhost:5000/uploads/placeholder.jpg';
    }
  };

  if (loading) {
    return (
      <div className="container mx-auto py-12 px-4 text-center">
        <h1 className="text-3xl font-bold mb-6">Photo Gallery</h1>
        <div className="text-gray-600">Loading photos...</div>
      </div>
    );
  }

  return (
    <div className="container mx-auto py-12 px-2 md:px-4">
      <h1 className="text-3xl font-bold mb-6">Photo Gallery</h1>
      {error && (
        <div className="text-red-600 mb-4 p-4 bg-red-50 rounded">{error}</div>
      )}
      {allEvents.length > 0 ? (
        <>
          {/* Event Navigation Bar */}
          <div className="flex overflow-x-auto gap-2 mb-8 pb-2 border-b border-gray-200 sticky top-16 z-10 bg-white/90">
            {allEvents.map((evt) => (
              <button
                key={evt}
                onClick={() => setSelectedEvent(evt)}
                className={`px-4 py-2 rounded-t-lg font-semibold whitespace-nowrap transition-colors duration-200 border-b-2 focus:outline-none ${
                  selectedEvent === evt
                    ? "bg-blue-100 text-blue-700 border-blue-600 shadow"
                    : "bg-gray-100 text-gray-600 border-transparent hover:bg-blue-50"
                }`}
              >
                {evt}
              </button>
            ))}
          </div>

          {/* Photos Grid */}
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
            {selectedEvent &&
              groupedPhotos[selectedEvent]?.map((photo) => (
                <div
                  key={photo._id}
                  className="relative aspect-square overflow-hidden rounded-lg shadow-md hover:shadow-xl transition-shadow duration-300"
                >
                  <img
                    src={photo.url}
                    alt={photo.title || photo.filename}
                    className="w-full h-full object-cover hover:scale-105 transition-transform duration-300"
                    onError={(e) => handleImageError(e, photo)}
                  />
                  {photo.description && (
                    <div className="absolute inset-0 bg-black bg-opacity-50 opacity-0 hover:opacity-100 transition-opacity duration-300 flex items-center justify-center p-4">
                      <p className="text-white text-center">
                        {photo.description}
                      </p>
                    </div>
                  )}
                </div>
              ))}
          </div>

          {/* Empty State for Selected Event */}
          {selectedEvent && (!groupedPhotos[selectedEvent] || groupedPhotos[selectedEvent].length === 0) && (
            <div className="text-center text-gray-400 py-8">
              No photos in this event yet.
            </div>
          )}
        </>
      ) : (
        <div className="text-center text-gray-600 py-8">
          <p>No photos available in the gallery yet.</p>
          <p className="mt-2">Check back later for updates!</p>
        </div>
      )}
    </div>
  );
};

export default Gallery;
