import { useState, useEffect } from 'react';

const moments = [
  {
    id: 1,
    image: '/images/sunrise-batch.jpg',
    caption: 'Sunrise Batch üåÑ',
    testimonial: '"The morning energy here is incredible. Best way to start my day!"'
  },
  {
    id: 2,
    image: '/images/community-circle.jpg',
    caption: 'Community Circle üßò',
    testimonial: '"Found my yoga family here. Such a supportive environment!"'
  },
  {
    id: 3,
    image: '/images/beginner-flow.jpg',
    caption: 'Beginner Flow üî∞',
    testimonial: '"Perfect for beginners. Patient guidance and steady progress."'
  }
];

const YogaMoments = () => {
  const [currentSlide, setCurrentSlide] = useState(0);

  // Auto-play functionality
  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentSlide((prev) => (prev + 1) % moments.length);
    }, 5000);

    return () => clearInterval(timer);
  }, []);

  const nextSlide = () => {
    setCurrentSlide((prev) => (prev + 1) % moments.length);
  };

  const prevSlide = () => {
    setCurrentSlide((prev) => (prev - 1 + moments.length) % moments.length);
  };

  return (
    <section className="py-16 bg-gray-50">
      <div className="container mx-auto px-4">
        <h2 className="text-4xl font-playfair text-center mb-12">Our Yoga Moments</h2>
        
        <div className="relative max-w-4xl mx-auto">
          {/* Carousel */}
          <div className="relative overflow-hidden rounded-2xl shadow-lg">
            {moments.map((moment, index) => (
              <div
                key={moment.id}
                className={`absolute w-full transition-all duration-500 transform ${
                  index === currentSlide
                    ? 'opacity-100 translate-x-0'
                    : index < currentSlide
                    ? 'opacity-0 -translate-x-full'
                    : 'opacity-0 translate-x-full'
                }`}
                style={{ zIndex: index === currentSlide ? 1 : 0 }}
              >
                <div className="relative aspect-video">
                  <img
                    src={moment.image}
                    alt={moment.caption}
                    className="w-full h-full object-cover"
                  />
                  {/* Caption and testimonial overlay */}
                  <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/80 to-transparent p-6 text-white">
                    <h3 className="text-2xl font-semibold mb-2">{moment.caption}</h3>
                    <p className="text-lg italic">{moment.testimonial}</p>
                  </div>
                </div>
              </div>
            ))}
          </div>

          {/* Navigation buttons */}
          <button
            onClick={prevSlide}
            className="absolute left-4 top-1/2 -translate-y-1/2 bg-white/80 hover:bg-white text-gray-800 p-2 rounded-full shadow-lg z-10 transition-all"
            aria-label="Previous slide"
          >
            ‚Üê
          </button>
          <button
            onClick={nextSlide}
            className="absolute right-4 top-1/2 -translate-y-1/2 bg-white/80 hover:bg-white text-gray-800 p-2 rounded-full shadow-lg z-10 transition-all"
            aria-label="Next slide"
          >
            ‚Üí
          </button>

          {/* Dots indicator */}
          <div className="absolute -bottom-8 left-0 right-0 flex justify-center gap-2">
            {moments.map((_, index) => (
              <button
                key={index}
                onClick={() => setCurrentSlide(index)}
                className={`w-2 h-2 rounded-full transition-all ${
                  index === currentSlide ? 'bg-orange-400 w-4' : 'bg-gray-300'
                }`}
                aria-label={`Go to slide ${index + 1}`}
              />
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default YogaMoments; 