import { useState, useEffect } from 'react';

const API_BASE_URL = 'http://localhost:5000';

export const useImageLoader = (imagePath, fallbackImage = '/uploads/placeholder.jpg') => {
  const [imageUrl, setImageUrl] = useState(null);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    let isMounted = true;
    let img = new Image();

    const loadImage = async () => {
      try {
        setIsLoading(true);
        setError(null);

        // Construct the full URL
        const fullUrl = imagePath?.startsWith('http') 
          ? imagePath 
          : `${API_BASE_URL}${imagePath || fallbackImage}`;

        // Create a promise to handle image loading
        await new Promise((resolve, reject) => {
          img.onload = resolve;
          img.onerror = reject;
          img.src = fullUrl;
        });

        if (isMounted) {
          setImageUrl(fullUrl);
        }
      } catch (err) {
        if (isMounted) {
          console.error('Error loading image:', err);
          setError(err);
          // Try loading the fallback image
          setImageUrl(`${API_BASE_URL}${fallbackImage}`);
        }
      } finally {
        if (isMounted) {
          setIsLoading(false);
        }
      }
    };

    loadImage();

    return () => {
      isMounted = false;
      // Cancel the image loading if component unmounts
      img.src = '';
    };
  }, [imagePath, fallbackImage]);

  return { imageUrl, error, isLoading };
};

export default useImageLoader; 